
import React, { Component } from 'react'

export class AccountSetup extends Component {
    continue = e => {
        e.preventDefault();
        this.props.nextStep();
    };

    render() {
        const { values, inputChange } = this.props;

        return (
            <div className="form-container">
                <h1 className="mb-5">Dane klienta</h1>
                <div className="form-group">
                    <label htmlFor="name">Nazwa klienta</label>
                    <input type="text" className="form-control" name="clientname" placeholder="Nazwa klienta" onChange={inputChange('clientname')} value={values.clientname} />
                </div>
                <div className="form-group">
                    <label htmlFor="email">Email</label>
                    <input type="email" className="form-control" name="email" placeholder="adres email" onChange={inputChange('email')} value={values.email} />
                </div>
                <div className="form-group">
                    <label htmlFor="phone">Numer telefonu</label>
                    <input type="number" className="form-control" name="phone" placeholder="+48 ..." onChange={inputChange('phone')} value={values.phone} />
                </div>
                <div className="form-group">
                    <label htmlFor="password">Osoba kontaktowa</label>
                    <input type="text" className="form-control" name="contactperson" onChange={inputChange('contactperson')} value={values.contactperson} />
                </div>

                <br />

                <div className="text-right">
                    <button type="button" class="btn btn-primary btn-lg btn-block" onClick={this.continue}>Oferta na okna</button>
                    <button type="button" class="btn btn-secondary btn-lg btn-block">Oferta na drzwi</button>
                </div>
            </div>
        )
    }
}

export default AccountSetup