  
import React, { Component } from 'react'

export class Confirm extends Component {
    continue = e => {
        e.preventDefault();
        this.props.nextStep();
    };

    back = e => {
        e.preventDefault();
        this.props.prevStep();
    };

    render() {
        const {
            values: { clientname, contactperson, email, phone, 
                windowtype, profile, windowpackage, windowcolor,
                windowquantity, windowwidth, windowheight}
        } = this.props;

        return (
            <div className="form-container">
                <h1 className="mb-5">Podsumowanie</h1>
                <ul class="list-group">
                    <li class="list-group-item">Nazwa klienta: {clientname}</li>
                    <li class="list-group-item">Email: {email}</li>
                    <li class="list-group-item">Telefon kontaktowy: {phone}</li>
                    <li class="list-group-item">Osoba kontaktowa: {contactperson}</li>
                    <li class="list-group-item">Typ okna: {windowtype}</li>
                    <li class="list-group-item">Profil: {profile}</li>
                    <li class="list-group-item">Pakiet szybowy: {windowpackage}</li>
                    <li class="list-group-item">Kolor: {windowcolor}</li>
                    <li class="list-group-item">Ilość: {windowquantity}</li>
                    <li class="list-group-item">Szerokość: {windowwidth}</li>
                    <li class="list-group-item">Wysokość: {windowheight}</li>
                </ul>

                <br /><br />

                <div className="row">
                    <div className="col-6">
                        <button className="btn btn-danger" onClick={this.back}>Powrót</button>
                    </div>
                    <div className="col-6 text-right">
                        <button className="btn btn-primary" onClick={this.continue}>Dalej</button>
                    </div>
                </div>
            </div>
        )
    }
}

export default Confirm