import React, { Component } from 'react';
import WindowType from './WindowTypeGallery'
import WindowProfile from './WindowProfile'

export class WindowsOffer extends Component {
    continue = e => {
        e.preventDefault();
        this.props.nextStep();
    };

    back = e => {
        e.preventDefault();
        this.props.prevStep();
    };

    render() {
        const { values, inputChange, handleOnChangeType, handleOnChange } = this.props;

        return (
            <div className="form-container">
            <h1 className="mb-5">Wycena okna</h1>
                <WindowType
                    inputChange={inputChange}
                    handleOnChangeType={handleOnChangeType}
                    values={values}                  
                />
                <WindowProfile
                    inputChange={inputChange}
                    handleOnChange={handleOnChange}
                    values={values}                  
                />
                <div className="form-group">
                    <label htmlFor="windowpackage">PAKIET SZYBOWY</label>
                    <input list="windowpackage" className="form-control" name="windowpackage"
                     onChange={inputChange('windowpackage')} value={values.windowpackage} />
                    <datalist id="windowpackage">
                        <option value="Dwuszybowy"/>
                        <option value="Trzyszybowy"/>
                        <option value="Antywłamaniowy 2 szybowy"/>
                    </datalist>
                </div>
                <div className="form-group">
                    <label htmlFor="windowcolor">KOLOR</label>
                    <input list="windowcolor" className="form-control" name="windowcolor" onChange={inputChange('windowcolor')} value={values.windowcolor} />
                    <datalist id="windowcolor">
                        <option value="Biały"/>
                        <option value="Orzech"/>
                        <option value="Jasno szary"/>
                    </datalist>
                </div>
                <div class="form-row">
                    <div class="col-md-6 mb-3">
                    <label for="windowquantity">Ilość</label>
                    <input type="number" className="form-control" name="windowquantity" onChange={inputChange('windowquantity')} value={values.windowquantity} />
                    </div>
                    <div class="col-md-3 mb-3">
                    <label for="windowwidth">Szerokość [mm]</label>
                    <input type="number" className="form-control" name="windowwidth" onChange={inputChange('windowwidth')} value={values.windowwidth} />
                    </div>
                    <div class="col-md-3 mb-3">
                    <label for="windowheight">Wysokość [mm]</label>
                    <input type="number" className="form-control" name="windowheight" onChange={inputChange('windowheight')} value={values.windowheight} />
                    </div>
                </div>

                <br />

                <div className="row">
                    <div className="col-6">
                        <button className="btn btn-danger" onClick={this.back}>Powrót</button>
                    </div>
                    <div className="col-6 text-right">
                        <button className="btn btn-primary" onClick={this.continue}>Dalej</button>
                    </div>
                </div>
            </div>
        )
    }
}

export default WindowsOffer