import {Component} from 'react';
import PropTypes from 'prop-types';
import Gallery from 'react-grid-gallery';

export class WindowProfile extends Component {

        constructor(props){
          super(props);

          this.state = {
              images: this.props.images
          };

          this.onSelectImage = this.onSelectImage.bind(this);

      }

      onSelectImage (index, image) {
          var images = this.state.images.slice();
          var img = images[index];
          if(img.hasOwnProperty("isSelected"))
              img.isSelected = !img.isSelected;
          else
              img.isSelected = true;

          this.setState({
              images: images
          });
      }
    
      render() {

        const { values, inputChange, handleOnChange } = this.props;

        return (
            <div>
            <div className="form-group">
               <label htmlFor="profile">PROFIL</label>
               <input list="profile" className="form-control" name="profile" 
               onChange={inputChange('profile')} value={values.profile} hidden/>
               <datalist id="profile">
                   <option value="Encore"/>
                   <option value="Ovlo Classic"/>
                   <option value="REHAU"/>
               </datalist>
                  <select className="form-control" 
                   onChange={handleOnChange} value={values.profile}>
                       <option value=""></option>
                       <option value="Encore">Encore</option>
                       <option value="Ovlo Classic">Ovlo Classic</option>
                       <option value="REHAU">REHAU</option>
                   </select>
               
    {
        (() => { 

            if(values.profileindex !== '') 
            this.props.images.map((image, index) => {
                if(index === values.profileindex){
                    return image.isSelected = true
                } else {
                    return image.isSelected = false
                }
            })


            })()
    }
      
       </div>
                  <div style={{
                    display: "block",
                    minHeight: "1px",
                    width: "100%",
                    border: "1px solid #ddd",
                    overflow: "auto"}}>
                <Gallery
                      images={this.state.images}
                      onSelectImage={this.onSelectImage}
                      lightboxWidth={1536}
                />
            </div>
          </div>
        )
      }
    
}

WindowProfile.propTypes = {
  images: PropTypes.arrayOf(
      PropTypes.shape({
          src: PropTypes.string.isRequired,
          thumbnail: PropTypes.string.isRequired,
          srcset: PropTypes.array,
          caption: PropTypes.oneOfType([
              PropTypes.string,
              PropTypes.element
          ]),
          thumbnailWidth: PropTypes.number.isRequired,
          thumbnailHeight: PropTypes.number.isRequired,
          isSelected: PropTypes.bool
      })
  ).isRequired
};

